<script>
	
  import Page1 from './Page1.svelte';
  import Page2 from './Page2.svelte';
  import RationCalculator from './RationCalculator';
  let rationCalculator;
	const pages = [Page1, Page2];
	let page = 0;

	let pagesState = [];
  let requiredVals;
	// Our handlers
	function onSubmit(values) {
    if (page === pages.length - 1) {
      // On our final page with POST our data somewhere
      //console.log('Submitted data: ', pagesState)
    } else {
      // If we're not on the last page, store our data and increase a step
	  
      pagesState[page] = values;
      pagesState = pagesState; // Triggering update
      rationCalculator = new RationCalculator(values["lactation"], values["yield"], values["fatRatio"], values["weekofLactation"], values["liveWeight"], values["gestationPeriod"])
      requiredVals = rationCalculator.calculateRation();
      page +=1;
    }
  }

  function onBack(values) {
    if (page === 0) return;
		pagesState[page] = values;
    pagesState = pagesState; // Triggering update
    page -= 1;
  }
</script>

<svelte:component
  this={pages[page]}
  {onSubmit}
  {onBack}
  initialValues={pagesState[page]}
  requiredVals={requiredVals}
/>
