<script>
    import MultiSelect from "svelte-multiselect";
    import { t, locale, locales } from "./i18n";
    export let optionsForMultiSelect;
    export let typeOfFeed = "";
    export let selected = [];
    export let prices = [];
    export let minVals = [];
    export let maxVals = [];
    export let minNumOfSel = 0;
    export let maxNumOfSel = -1;
    function addTableRow(event) {
        prices[event.detail.option] = 0.1;
        minVals[event.detail.option] = 0;
        maxVals[event.detail.option] = 99999;
    }
    function removeTableRow(event) {
        delete prices[event.detail.option];
        delete minVals[event.detail.option];
        delete maxVals[event.detail.option];
    }
    
</script>

<main>
    <label for={typeOfFeed}>{typeOfFeed}</label>
    {#if maxNumOfSel>-1}
    <MultiSelect
        id={typeOfFeed}
        bind:selected
        options={optionsForMultiSelect}
        maxSelect={maxNumOfSel}
        on:add={(event) => addTableRow(event)}
        on:remove={(event) => removeTableRow(event)}
    />
    {:else}
    <MultiSelect
        id={typeOfFeed}
        bind:selected
        options={optionsForMultiSelect}
        on:add={(event) => addTableRow(event)}
        on:remove={(event) => removeTableRow(event)}
    />
    {/if}
    {#each selected as option, i}
        <p>{option} </p>
        <table>
            <tr>
                <th> {$t("price")} </th>
                <td
                    ><input
                        id={option + "fiyat"}
                        name={option + "fiyat"}
                        type="number"
                        bind:value={prices[option]}
                        min="0.1"
                    /></td
                >
                <th> {$t("minimum")} </th>
                <td
                    ><input
                        id={option + "min"}
                        name={option + "min"}
                        type="number"
                        bind:value={minVals[option]}
                        min="0"
                    /></td
                >
                <th> {$t("maximum")} </th>
                <td
                    ><input
                        id={option + "max"}
                        name={option + "max"}
                        type="number"
                        bind:value={maxVals[option]}
                        min="0"
                    /></td
                >
            </tr>
        </table>
    {/each}
</main>

<style>
    table {
        width: 100%;
        border-collapse: collapse;
    }

    th {
        font-weight: normal;
        font-size: 14px;
        text-align: left;
        border: 1px solid #ccc;
        padding: 8px;
    }

    td {
        font-size: 16px;
        border: 1px solid #ccc;
        padding: 8px;
    }

    input {
        width: 100%;
        border: none;
        padding: 8px;
    }

    label {
        text-transform: uppercase;
        font-weight: bold;
        font-size: 25px;
    }
    p {
        text-transform: uppercase;
        font-size: 18px;
    }
</style>
