<script>
	import { createForm } from 'felte'
	import MultiSelectCustom from './MultiSelectCustom.svelte';
	import { t, locale, locales } from "./i18n";
	import dataFeed from './options';
	import dataFeedEn from './optionsen';
	import { writable } from 'svelte/store';
	import Modal, { bind } from 'svelte-simple-modal';
	import Popup from './Popup.svelte';
	import Warning from './Warning.svelte'
	const modal = writable(null);

	export let initialValues;
	export let onSubmit;
	export let onBack;
	export let requiredVals;
	const { form, data } = createForm({ onSubmit, initialValues })
	let tndclasses = ['Kaba yem', 'Diğer Yem', 'Enerji', 'Protein', 'Karma']
	let dataSelect = dataFeed;
	if($t("language") === "en") {
		dataSelect = dataFeedEn;
	}
	//const multiSelectOptions = dataFeed.map(option => option["feed"])
	const multiSelectOptionsKaba = dataSelect.filter(element => element["TDNClass"]===tndclasses[0]).map(option => option["feed"])
	let selectedKaba = []
	let pricesKaba = [];
    let minValsKaba = [];
    let maxValsKaba = [];
	const multiSelectOptionsDiger = dataSelect.filter(element => element["TDNClass"]===tndclasses[1]).map(option => option["feed"])
	let selectedDiger = []
	let pricesDiger = [];
    let minValsDiger = [];
    let maxValsDiger = [];
	const multiSelectOptionsEnerji = dataSelect.filter(element => element["TDNClass"]===tndclasses[2]).map(option => option["feed"])
	let selectedEnerji = []
	let pricesEnerji = [];
    let minValsEnerji = [];
    let maxValsEnerji= [];
	const multiSelectOptionsProtein = dataSelect.filter(element => element["TDNClass"]===tndclasses[3]).map(option => option["feed"])
	let selectedProtein = []
	let pricesProtein = [];
    let minValsProtein = [];
    let maxValsProtein = [];
	const multiSelectOptionsKarma = dataSelect.filter(element => element["TDNClass"]===tndclasses[4]).map(option => option["feed"])
	let selectedKarma = [];
	let pricesKarma = [];
    let minValsKarma = [];
    let maxValsKarma = [];
	//console.log({requiredVals})
	
	async function denemeFunc() {
		if(selectedKaba.length<1) {
			modal.set(bind(Warning, { message: $t("warning.forage")}));
		}
		else {
			let selectedOpts = selectedKaba.concat(selectedDiger,selectedEnerji,selectedProtein,selectedKarma)
			let prices = {...pricesKaba, ...pricesDiger, ...pricesEnerji, ...pricesProtein, ...pricesKarma}
			let minVals = {...minValsKaba, ...minValsDiger, ...minValsEnerji, ...minValsProtein, ...minValsKarma}
			let maxVals = {...maxValsKaba, ...maxValsDiger, ...maxValsEnerji, ...maxValsProtein, ...maxValsKarma}
			let model = {};
			selectedOpts.forEach((option) => {
				model[option] = {
					price: prices[option],
					min: minVals[option],
					max: maxVals[option]
				};
			});
			//console.log(model)
			const body = {"m": model, "r": requiredVals}
			const response = await fetch('https://api.dairyrasyon.com/solve', {
				method: 'POST',
				headers: { 'Content-Type': 'application/json' },
				body: JSON.stringify(body)
			});
			const json = await response.json();
			//console.log(json)
			if(json["status"] === "optimal")
				modal.set(bind(Popup, { feeds: json["feeds"], masses: json["values"] }));
			else
				modal.set(bind(Warning, { message: $t("warning.infeasible")}));
				//modal.set(bind(Warning, { message: json["status"]}));
		}
		//optimizePrice(requiredVals, prices, selectedOpts)
	}

</script>

<main>
	<h1>{$t("homepage.title")}</h1>
	<form use:form>
		<MultiSelectCustom
			typeOfFeed={$t("feed.forage")}
			optionsForMultiSelect={multiSelectOptionsKaba}
			bind:selected={selectedKaba}
			bind:prices={pricesKaba}
			bind:minVals={minValsKaba}
			bind:maxVals={maxValsKaba}
			minNumOfSel={1}
			maxNumOfSel={6}
		/>

		<MultiSelectCustom
			typeOfFeed={$t("feed.mixed")}
			optionsForMultiSelect={multiSelectOptionsKarma}
			bind:selected={selectedKarma}
			bind:prices={pricesKarma}
			bind:minVals={minValsKarma}
			bind:maxVals={maxValsKarma}
			minNumOfSel={0}
			maxNumOfSel={6}
		/>

		<MultiSelectCustom
			typeOfFeed={$t("feed.energy")}
			optionsForMultiSelect={multiSelectOptionsEnerji}
			bind:selected={selectedEnerji}
			bind:prices={pricesEnerji}
			bind:minVals={minValsEnerji}
			bind:maxVals={maxValsEnerji}
			minNumOfSel={0}
			maxNumOfSel={6}
		/>

		<MultiSelectCustom
			typeOfFeed={$t("feed.protein")}
			optionsForMultiSelect={multiSelectOptionsProtein}
			bind:selected={selectedProtein}
			bind:prices={pricesProtein}
			bind:minVals={minValsProtein}
			bind:maxVals={maxValsProtein}
			minNumOfSel={0}
			maxNumOfSel={6}
		/>

		<MultiSelectCustom
			typeOfFeed={$t("feed.other")}
			optionsForMultiSelect={multiSelectOptionsDiger}
			bind:selected={selectedDiger}
			bind:prices={pricesDiger}
			bind:minVals={minValsDiger}
			bind:maxVals={maxValsDiger}
			minNumOfSel={0}
			maxNumOfSel={3}
		/>
		<br />
		<br />
		<button type="button" on:click={() => onBack($data)}>{$t("button.back")}</button>
		<button type="button" on:click={denemeFunc}>{$t("button.calculate")}</button>
	</form>
	<br />
	<br />
	<footer>
		<a href="https://scholar.google.com/citations?user=lG6FPbwAAAAJ&hl=en&oi=ao">{$t("createdBy")}</a> {$t("contact")}

   </footer>
	<Modal show={$modal}>
	</Modal>
</main>

<style>
	main {
		text-align: center;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}

	h1 {
		color: #ff3e00;
		text-transform: uppercase;
		font-size: 4em;
		font-weight: 100;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>
